.home {
    /* background-image: url(/reactApp/src/images/ikorodu.jpg); */
    background: linear-gradient(45deg, rgb(214, 86, 86), rgb(221, 172, 172));
    background-attachment: fixed;
    height: 90vh;
    margin-top: -5px;
    padding-top: 30px;
}

h1 {
    font-size: 65px;
    font-weight: 900;
    margin-bottom: 75px;
    color: rgb(50, 33, 33);
}

.intro {
    width: 800px;
    margin: auto;
    font-size: x-large;
    padding: 50px;
    background-color: rgb(221, 172, 172);
    border-radius: 20px;
    text-align: justify;
}

.icon-container ul {
    display: flex;
    justify-content: center;
    gap: 20px;
}

